<script> 
// import { VueEditor } from "vue2-editor";
export default {
    props:{
        id:{}
    },
    components: {
        // VueEditor
    },
    data(){
        return {
            model:{
                projects:[],
                awards:[],
            },
            parents:[],
            recruits:[],
            choose_recruits:[],
            admin:[]
        }
    },
    methods: {
        afterUpload(res){
            this.$set(this.model,'avator',res.url)
            console.log(res)
        },
        async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
            console.log(1);
            const formData = new FormData();
            formData.append("file", file);
            const res = await this.$http.post("upload", formData);
            Editor.insertEmbed(cursorLocation, "image", res.data.url);
            resetUploader();
        },
        async save(){
            console.log(this.model)
            if(this.id){
                await this.$http.put(`/rest/vitaes/${this.id}`,this.model)
            }else{
                await this.$http.post('/rest/vitaes',this.model);
            }
            this.$router.push('/vitaes/list')
            this.$message({
                type:"success",
                message:"success" 
            })
        },
        async fetch(){
            const res = await this.$http.get(`/rest/vitaes/${this.id}`)
            this.model = res.data
            
        },
        async fetchCategories(){
            const res = await this.$http.get(`/rest/recruits`)
            this.recruits = res.data
            const admin = await this.$http.get('/rest/admin_users')
            this.admin = admin.data
        }
    },
    created(){
        this.fetchCategories()
        this.id&&this.fetch()
    }
}
</script>
